function List() {
  const [currentPage, setCurrentPage] = useState(1)
  const [postsPerPage, setPostsPerPage] = useState(180)
  const [fetchParams, setFetchParams] = useState({
    data: [],
    loading: true
  })

  const paginate = (number) => {
    setCurrentPage(number)
    window.scrollTo(0, 0);
  }

  useEffect(() => {
    const fetchPokemons = async () => {
      const res = await axios.get(url)
      setFetchParams({
        ...fetchParams,
        data: res.data.results.sort(function(a, b){
          if(a.name < b.name) { return -1; }
          if(a.name > b.nmae) { return 1; }
          return 0;
        }),
        loading: false
      })
    } // async
    fetchPokemons()
  }, [])

  const indexOfLastPokemon = currentPage * postsPerPage
  const indexOfFirstPokemon = indexOfLastPokemon - postsPerPage
  var currentPokemons
  if (fetchParams.data.length != 0) {
    currentPokemons = fetchParams.data.slice(indexOfFirstPokemon, indexOfLastPokemon)
  }

  return (
    <>
    <h2 className="main-title">Все покемоны</h2>
    <hr className="divider"/>
    {
      fetchParams.loading ? <h1 className="loading">Загрузка</h1> : (
        <>
        <ListItems pokemons={currentPokemons} />
        <Pagination postsPerPage={postsPerPage} allPosts={fetchParams.data.length} paginate={paginate}/>
        </>)
    }
    </>
  )
}

export default List
