import React from 'react'
import {  Link  } from "react-router-dom";

import MainCard from './MainCard.js'

const pokemonArray = [
        {name: "pikachu", num: 25},
        {name: "bulbasaur", num: 1},
        {name: "charmander", num: 6},
        {name: "tyranitar", num: 248},
        {name: "gardevoir", num: 282},
        {name: "rayquaza", num: 384},
        {name: "garchomp", num: 445},
        {name: "sylveon", num: 700},
        {name: "umbreon", num: 197},
        {name: "lugia", num: 249},
        {name: "lucario", num: 448},
        {name: "pidgey", num: 16}
]

function Main() {
    return (
        <>
        <h2 className="main-title">Главная</h2>
        <hr className="divider"/>
            <div className="container">
            {
                pokemonArray.map((element) => <MainCard key={element.name} name={element.name} num={element.num} />)
            }
            </div>
        </>
    )
}

export default Main
