import React from 'react'
import {  Link  } from "react-router-dom";

function MainCard({name, num}) {
    return (
        <div className="card">
            <Link to={"name/"+name}>
                <div className="front">
                    <img className="card-img" src={"https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/other/official-artwork/"+num+".png"}/>
                    <div className="name-con">
                        <div className="name">{name}</div>
                    </div>
                </div>
            </Link>
        </div>
    )
}

export default MainCard
