import React from 'react'
import {  NavLink  } from "react-router-dom";

function Header() {
    return (
        <>
            <div className="header">
                <div className="nav-con">
                    <NavLink  className="home-con" exact activeClassName="active" to="/" > Главная  </NavLink >
                    <NavLink  className="all-con" activeClassName="active" to="/all" > Все покемоны </NavLink >
                </div>
            </div>

        </>
    )
}

export default Header
